@media (max-width: 768px) {
  .club-sms {
    margin: 10px 0 40px;
  }
}
@media (max-width: 500px) {
  .modal__new {
    max-width: none;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 60%;
    height: auto;
    display: flex;
    flex-flow: column;
    justify-content: center;
  }
}
</style>

